<template>
<v-layout>
<v-navigation-drawer v-model="drawer" absolute temporary light >
  <v-list>
    <v-list-tile>
      <v-list-tile-content>{{ user.username }}</v-list-tile-content>
    </v-list-tile>
    <v-list-tile @click="signOut">
      <v-list-tile-title>Sign Out</v-list-tile-title>
    </v-list-tile>
  </v-list>
</v-navigation-drawer>
<v-toolbar color="#228b22" dark fixed>
    <v-btn icon @click.stop="drawer = !drawer">
        <v-icon>mdi-menu</v-icon>
    </v-btn>
    <v-toolbar-title>GreenNinja BudFinder</v-toolbar-title>
    <v-spacer></v-spacer>
    <v-text-field label="Enter Search..." v-if="showSearch" @keyup="searchJS">
    </v-text-field>
    <v-btn icon>
        <v-icon @click="showSearch = !showSearch">mdi-magnify</v-icon>
    </v-btn>
    <v-btn icon>
        <v-icon>mdi-select-compare</v-icon>
    </v-btn>
    <v-btn icon @click="dialog = true">
     <v-icon>mdi-filter-variant</v-icon>
    </v-btn>
</v-toolbar>
    <v-dialog v-model="dialog" transition="dialog-bottom-transition">
      <v-card >
        <v-toolbar color="#228b22" dark>
          <v-btn icon dark @click="dialog=false">
            <v-icon>mdi-close</v-icon>
          </v-btn>
          <v-toolbar-title>Select Filters</v-toolbar-title>
        </v-toolbar>
        <v-card-text  style="opacity:0.7;filter:alpha(opacity=70)"  >
          <div>
            <v-btn>Hybrid</v-btn>
            <v-btn>Indica</v-btn>
          </div>
          <div>
            <v-btn>Vapes</v-btn>
            <v-btn>Flowers</v-btn>
          </div>
          <div>
            <v-btn v-on:click="setFilter('harborsidesanjose')">Harborside San Jose</v-btn>
            <v-btn v-on:click="setFilter('elementalwellness')">Elemental Wellness</v-btn>
            <v-btn v-on:click="setFilter('airfield')">Airfield</v-btn>
          </div>
          <div>
            <v-btn v-on:click="clearFilters">Clear Filters</v-btn>
          </div>
        </v-card-text>
      </v-card>
    </v-dialog>
</v-layout>
</template>

<script>
import { mapGetters, mapActions, mapState } from 'vuex'
export default {
  name: 'Navbar',
  props: ['user'],
  data () {
    return {
      blockstack: window.blockstack,
      dialog: false,
      drawer: true,
      showSearch: false
    }
  },
  computed: {
    ...mapGetters(['getFilteredPosts', 'filterExists']),
    ...mapState(['filters'])
  },
  watch: {
    filters: function (val) {
      if (this.filters.length > 0) {
        this.setFilteredPosts2(this.filters)
      } else {
        this.fetchPosts()
      }
    }
  },
  methods: {
    signOut () {
      this.blockstack.signUserOut('http://localhost:8080/#/')
      this.$emit('update:user', null)
    },
    setFilter: function (filter) {
      console.log(this.filters.indexOf(filter))
      console.log(this.filterExists(filter))
      if (!this.filterExists(filter)) this.$store.commit('addFilter', filter)
      console.log(this.filters)
    },
    searchJS (event) {
      this.setFilteredPosts(event.target.value)
    },
    clearFilters: function () {
      this.$store.dispatch('clearFilters')
    },
    ...mapActions(['setFilteredPosts', 'setFilteredPosts2', 'fetchPosts'])
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



