import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import TorrentList from '@/components/TorrentList'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'TorrentList',
      component: TorrentList
    },
    {
      path: '/hello',
      name: 'HelloWorld',
      component: HelloWorld
    }
  ]
})



